import React from "react"

import Header from "../../components/header.js"
import "./portfolio.css"

const Portfolio = () => {
  return (
    <div className="portfolio">
      <Header />
      <div className="link">
        <a href="https://youtu.be/BLfYnxjTUnk">React Query Presentation</a>
        <p>
          Video of a presentation I gave at the CharlestonJS Meetup on April 1,
          2021.
        </p>
      </div>
      <div className="link">
        <a href="https://zoomchsride.com">Zoom Charleston's Rideshare</a>
        <p>
          The Client facing portion of the Web Application utilizes React.js,
          HTML, CSS and JavaScript. Backend REST APIs were constructed with
          NodeJS and ExpressJS and hosted on an Amazon Lightsail Instance with a
          Nginx reverse proxy server.
        </p>
      </div>
      <div className="link">
        <a href="https://scottsandman.github.io/">This Site</a>
        <p>
          Yes, the site you are currently on. It was built during my time at JRS
          Code School, but was not an assignment. I started this blog as a means
          to "learn in public". I started, developed and deployed the initial
          version of the site in under 24 hours. It is built utilizing a
          Jamstack featuring Gatsby, the React based framework, and hosted on
          Github.
        </p>
      </div>
      <div className="link">
        <a href="https://master.d302zpp58vbn99.amplifyapp.com/" target="_blank">
          Small Business Connect - JRS Final Project
        </a>
        <p>
          Web Application to aid small business owners with connecting to local
          clientele with targeted advertising. The app utilizes geo location
          services to display ads to users when they are in range of the small
          business. Feel free to sign-up for both user and business accounts to
          see how the application works. A link to 10 Minute Mail has been
          provided so a temporary email address can be utilized in lieu of your
          own.
          <ul>
            <li>React.js for front end framework</li>
            <li>React Hooks for local state management</li>
            <li>
              Express Server utilizing serverless framework for deployment to
              AWS Lambda
            </li>
            <li>MongoDB Atlas to store application data </li>
            <li>
              AWS Lambda function to automatically assign user to Cognito groups
              on successful sign up
            </li>
            <li>
              Custom AWS Cognito Components for authentication and authorization
            </li>
            <li>Socket.io to provide application with real-time data</li>
          </ul>
        </p>
      </div>
      <div className="link">
        <a href="https://tiasandman.com" target="_blank">
          Tia Sandman Dance Resume
        </a>
        <p>
          Single Page Web Application for professional dancer's online resume
          <ul>
            <li>HTML and CSS to display application</li>
            <li>
              Javascript and jQuery to render application and route transitions
            </li>
            <li>Bootstrap for layout and responsive design</li>
          </ul>
        </p>
      </div>
      <div className="link">
        <a
          href="https://scottsandman.github.io/LetterkennyAPI/"
          target="_blank"
        >
          Letterkenny
        </a>
        <p>
          Mock website built to demonstrate knowledge of retrieving data from
          APIs. All information related to the Letterkenny program was retrieved
          from TVmaze.com.
          <ul>
            <li>HTML and CSS to display application</li>
            <li>
              Javascript to render application and route transitions and make
              API calls with fetch command
            </li>
          </ul>
        </p>
      </div>
      <div className="link">
        <a
          href="https://scottsandman.github.io/jrsrestaurantHTML/"
          target="_blank"
        >
          Joey Tomatoes
        </a>
        <p>
          Mock restaurant website built to demonstrate knowledge of HTML and
          CSS.
          <ul>
            <li>HTML and CSS to display application</li>
          </ul>
        </p>
      </div>
    </div>
  )
}

export default Portfolio
